import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'
import { IPerson } from '../../types/Person'
import { URL_API_V1 } from '..'
import { RootState } from '../..'
export const personApi = createApi({
    reducerPath: 'personApi',
    tagTypes: ['Person'],
    baseQuery: fetchBaseQuery({
        baseUrl: URL_API_V1,
        prepareHeaders: (headers, { getState }) => {
          const token = (getState() as RootState).auth.token
          if (token) {
            headers.set('authorization', `Bearer ${token}`)
          }
          return headers
        },
      }),
    endpoints: (builder) => ({
        getFindPeopleById: builder.query<IPerson, string>({
            query: (id: string | undefined) => `People/FindPeopleById/${id}`,
            providesTags:[{type:"Person", id:"List"}]
        }),
        getListPeople: builder.query<IPerson[], void>({
            query: () => `People`,
            providesTags:[{type:"Person", id:"List"}]
        }),
        createPeople: builder.mutation<IPerson, IPerson>({
            query: (person:any) => ({
                url: 'People',
                method: "POST",
                body: {person:person,tags:[]}
            }),
            invalidatesTags: (result, error, { id }) => [{ type: 'Person', id:"List" }],
        }),
        updatePeople: builder.mutation<IPerson, Partial<IPerson>>({
            query: (person) => ({
                url: 'People',
                method: "PUT",
                body: person
            }),
            invalidatesTags: (result, error, { id }) => [{ type: 'Person', id:"List" }],
        }),
        deletePeople: builder.mutation<string,string>({
            query: (id) => ({
                url: `People/${id}`,
                method: "DELETE",
                body: {id:id}
            }),
            invalidatesTags:  [{ type: 'Person', id:"List" }],
        })
    })
});
export const { 
    useGetFindPeopleByIdQuery, 
    useGetListPeopleQuery,
    useCreatePeopleMutation,
    useUpdatePeopleMutation,
    useDeletePeopleMutation
} = personApi;