import { ListItem, ListItemButton, ListItemIcon, ListItemText } from '@mui/material';
import * as React from 'react';
import { useNavigate } from 'react-router-dom';
interface ItemToProps {
    to:string,
    text:string,
    open?:boolean,
    children: JSX.Element
}
 
const ItemTo: React.FunctionComponent<ItemToProps> = ({to,text,open,children}) => {
    const navigate = useNavigate();
    const handleto = (url:string) => {
        navigate(url);
    };
    return ( <ListItem key={text} disablePadding sx={{ display: 'block' }}>
    <ListItemButton onClick={()=>handleto(to)}
        sx={{
            minHeight: 20,
            minWidth:150,
            justifyContent: open ? 'initial' : 'center',
            px: 2.5,
        }}
    >
        <ListItemIcon
            sx={{
                minWidth: 0,
                mr: open ? 3 : 'auto',
                justifyContent: 'center',
            }}
        >
            {children}
        </ListItemIcon>
        <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
    </ListItemButton>
</ListItem> );
}
 
export default ItemTo;